@import "./_job.less";
@job-img-dir: 'job/_images';

.job-name{
	padding:25*@rex;
	overflow:hidden;
	color:#fff;
	background: data-uri('@{job-img-dir}/cneter-bg.jpg');
	background-size: 100% 100%;

	.pre{
		width:180*@rex;
		height:auto;
		overflow:hidden;
		margin:0 auto;
		text-align:center;
		position:relative;

		span{
			display:block;
			font-size:20*@rex;
			line-height:53*@rex;
			float:left;
			text-align:center;
			padding-left:40*@rex;
		}

		i{
			display:block;
			width:33*@rex;
			height:33*@rex;
			background: data-uri('@{job-img-dir}/center-pc.png');
			background-size: 100% 100%;
			text-align:center;
			position:absolute;
			float:left;
			top:15%;
		}
	}
}

.project{
	margin-top:5%;
	overflow:hidden;
	background:#fff;

	li{
		display:block;
		border-top:1*@rex solid #EEEEEE;
		width:100%;
		float:left;
		line-height:55*@rex;

		.resume{
			display:block;
			.size(24*@rex,27*@rex);
			background: data-uri('@{job-img-dir}/ico.png');
			background-size: 100% 100%;
			.margin(13*@rex,10*@rex,_,10*@rex);
			float:left;
		}

		.password-ico{
			display:block;
			.size(22*@rex,25*@rex);
			background: data-uri('@{job-img-dir}/ico-1.png');
			background-size: 100% 100%;
			.margin(13*@rex,10*@rex,_,10*@rex);
			float:left;
			font-style:normal;
		}

		.position{
			display:block;
			width:25*@rex;
			height:25*@rex;
			background: data-uri('@{job-img-dir}/ico-2.png');
			background-size: 100% 100%;
			margin:15*@rex 10*@rex 10*@rex 10*@rex;
			float:left;
		}

		span{
			display:block;
			color:#333333;
			float:left;
			line-height:55*@rex;
			font-size:16*@rex;
		}

		ins{
			background: data-uri('@{job-img-dir}/sanjiao.png');
			background-size: 100% 100%;
			float:right;
			width:8*@rex;
			height:20*@rex;
			margin:15*@rex 10*@rex 0 0;

		}

	}
}